import React from "react";
import { useShortAnswerInput } from "react-google-forms-hooks";

export default function ShortAnswerInput({
  id,
  type,
  required,
  className,
}: {
  id: string;
  type: string;
  required: boolean;
  className: string;
}) {
  const { register } = useShortAnswerInput(id);
  return (
    <input
      id={id}
      type={type}
      {...register()}
      required={required}
      className={className}
    />
  );
}
