import React from "react";
import { useLongAnswerInput } from "react-google-forms-hooks";

export default function LongAnswerInput({
  id,
  required,
  rows,
  className,
}: {
  id: string;
  required: boolean;
  rows: number;
  className: string;
}) {
  const { register } = useLongAnswerInput(id);
  return (
    <textarea
      {...register()}
      required={required}
      id={id}
      rows={rows}
      className={className}
    />
  );
}
